.players-details-wrap {
  width: 20%;
  margin-left: 0px;
  margin-right: 0px;
  padding-top: 5%;
  border-radius: 0px 0px 0 0;
  box-shadow: 0 0 2px #ECECEC;
  -moz-box-shadow: 0 0 2px  rgba(0,0,0,0.2);
  -webkit-box-shadow: 0 0 2px  rgba(0,0,0,0.2);
  -o-box-shadow: 0 0 2px  rgba(0,0,0,0.2);
}

.players-details {
  display: flex;
  flex-direction: column;
  padding: 8%;
  padding-top: 0;
  padding-bottom: 13%;
}

.players-score-head {
  display: flex;
  justify-content: center;
}

.score {
  font-weight: bold;
  font-size: 3rem;
  font-family: BebasNeue;
  line-height: normal;
  padding-top: 5px;
}

.players-score-ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.score-li {
  display: flex;
  justify-content: space-between;
  font-family: BebasNeue;
  line-height: normal;
}

.player-position {
  display: flex;
  flex-direction: column-reverse;
  margin-left: 8px;
  padding-bottom: 8px;
  font-family: BebasNeue;
  line-height: normal;
}

.player-name {
  font-weight: 700;
  text-align: center;
  font-size: 1.2rem;
  font-family: BebasNeue;
  line-height: normal;
}

.players-image {
  display: inline-block;
  overflow: hidden;
  cursor: pointer;
}

.players-image img {
  width: 200%;
  height: 100%;
  object-fit:cover;
  object-position: center;
  overflow: hidden;
  margin-left: -50%;
}

.player-popview-remove {
  z-index: 1000;
  position: absolute;
  width: 100%;
  height: 100%;
  color: transparent;
  top: 0;
}

@media(max-width: 500px) {
  .score {
    font-size: 2rem;
    font-family: BebasNeue;
    line-height: normal;
  }

  .player-position {
    margin-left: 2px;
    padding-bottom: 3%;
  }

  .player-name {
    font-size: 0.7rem;
    font-family: BebasNeue;
    line-height: normal;
  }

  .players-score-ul {
    font-size: 0.7rem;
    font-family: BebasNeue;
    line-height: normal;
  }
}

@media(max-width: 300px) {
  .score {
    font-size: 1.5rem;
    font-family: BebasNeue;
    line-height: normal;
  }

  .player-position {
    margin-left: 2px;
    padding-bottom: 3%;
  }

  .player-name {
    font-size: 0.56rem;
    font-family: BebasNeue;
  }

  .players-score-ul {
    font-size: 0.5rem;
    font-family: BebasNeue;
  }
}