$bgcolor: lightblue;
$textcolor: black;
$fontsize: 18px;
$font-family: BebasNeue;

$iphone: 500px;
$android: 650px;
$tablet: 768px;
$desktop: 1080px;
$desktopHalf: 1400px;

/** For mobile part **/
@mixin iphone {
    @media (min-width : #{iphone}), screen and (origntation:portrait)  {
        @content;
    }
}

@mixin android {
    @media (min-width : #{android}), screen and (origntation:portrait)  {
        @content;
    }
}

/**For Desktop **/
@mixin tablet {
    @media screen and (min-width : #{$tablet}) and (max-width : $desktop) {
        @content;
    }
}

@mixin desktopHalf {
  @media screen and (min-width : #{$desktop}) and (max-width : $desktopHalf) {
      @content;
  }
}

/**For Desktop **/
@mixin desktopFull {
    @media screen and (min-width : #{$desktopHalf})  {
        @content;
    }
}

.App {
  color: $textcolor;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.app-wrap{
  position: relative;
}

.container {
  width: 100%;
  border-radius: 0 0 0 0; //42px 42px 0 0
  margin-top: 40px;
  background: white;
  @include desktopFull {
    width: 35%;
    margin-left: 32.5%;
  }

  @include desktopHalf() {
    width: 45%;
    margin-left: 27.5%;
  }

  @include tablet{
    width: 70%;
    margin-left: 15%;
  }
}

.indicator-container {
  position: fixed;
  width: 100%;
  margin-top: 50%;
  margin-left: 50%;
  @include desktopHalf{
    margin-top: 30%;
  }

  @include desktopFull {
    margin-top: 30%;
  }
}

@media(max-width: 500px) {
  .rival-team-or-quarter-value {
    font-size: 2rem;
  }

  .rival-team-or-quarter {
    font-size: 0.8rem;
  }
}

@font-face {
  font-family: 'BebasNeue';
  src: local('BebasNeue'), url(../../fonts/BebasNeue/TTF/BebasNeue-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'BebasNeue-bold';
  src: local('BebasNeue'), url(../../fonts/BebasNeue/TTF/BebasNeue-Bold.ttf) format('truetype');
}

@font-face {
  font-family: 'Helvetica-light';
  src: local('Helvetica'), url(../../fonts/Helvetica/helvetica-light.ttf) format('truetype');
}

@mixin gradient {
  background: #ffc65a;
  background: -moz-linear-gradient(left,  #ffc65a 0%, #ffa478 100%);
  background: -webkit-linear-gradient(left,  #ffc65a 0%,#ffa478 100%);
  background: linear-gradient(to right,  #ffc65a 0%,#ffa478 100%);
}

@mixin shop_item_gradient {
  background: #F4F3F2;
  background: -moz-linear-gradient(left,  #F4F3F2 0%, #e7e6e5 100%);
  background: -webkit-linear-gradient(left,  #F4F3F2 0%,#e7e6e5 100%);
  background: linear-gradient(to right,  #F4F3F2 0%,#e7e6e5 100%, to bottom); 
}