
@import "../../views/App/app.scss";

.event-history {
    display: block;
    background-color: #282828;
    padding: 10px 0;
}

.event-history-current {
    background-color: #282828;
    color: #ffffff;
    padding-left: 10px;
    padding-right: 10px;
    line-height: normal;
    font-family: BebasNeue;
    padding-bottom: 1%;
    white-space: nowrap;
    text-overflow:ellipsis;
    font-size: 1rem;
    overflow: hidden;
}

.event-history-timeline-container {
    display: flex;
    justify-content: space-between;
    margin-left: 10px;
    margin-right: 10px;
    padding-bottom: 0%;
    padding-top: 0%;
    overflow: hidden;
}

.event-history-timeline {
    display: block;
    width: 100%; //35%
    height: 100%;
    margin-right: 10px;
    background: #ffc65a;
    background: -moz-linear-gradient(left,  #ffc65a 0%, #ffa478 100%);
    background: -webkit-linear-gradient(left,  #ffc65a 0%,#ffa478 100%);
    background: linear-gradient(to right,  #ffc65a 0%,#ffa478 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffc65a', endColorstr='#ffa478',GradientType=1 );
}

.event-history-live {
    display: flex;
    width: 35%; //     width: 65%;
    // margin-right: 25%;
    color: #000000;
    font-size: 1.5rem;
    font-weight: 600;
    font-family: BebasNeue;
    line-height: 80px;
    text-align: center;
    flex-direction: row;
    justify-content: center;
    border-radius: 10px 0px 0px 10px;
    background: #ffc65a;
    background: -moz-linear-gradient(left,  #ffc65a 0%, #ffa478 100%);
    background: -webkit-linear-gradient(left,  #ffc65a 0%,#ffa478 100%);
    background: linear-gradient(to right,  #ffc65a 0%,#ffa478 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffc65a', endColorstr='#ffa478',GradientType=1 );
}

.event-history-live-red {
    width: 16px;
    height: 100%;
    margin: auto 0;
    align-items: center;
    text-align: center;
    border-radius: 8px 8px 8px 8px;
    background-color: ffffff00;
    font-size: 80px;
    color: #fff;
}

.event-history-live-txt {
    display: flex;
    flex-direction: column;
}

.event-history-live-red-dot {
    width: 16px;
    height: 16px;
    top: 50%;
    position: relative;
    border-radius: 50%;
    background-color: rgb(220, 41, 41);
    box-shadow: 0 0 8px rgba(248, 248, 248, 0.705);
    -webkit-transform: translate(-50%, -60%);
    -ms-transform: translate(-50%, -60%);
    transform: translate(-50%, -60%);
  }

.vis-grid.vis-vertical.vis-minor.vis-odd {
    top: 12px;
    height: 10px !important;
}

.vis-grid.vis-vertical.vis-minor.vis-even {
    margin-top: 6px;
    height: 22px !important;
}

