.vis-timeline {
    border: 0px !important;
    // height: 84px !important;
}

.vis-panel.vis-center {
    border-top-style: hidden !important;
    border-bottom-style: hidden !important;
    overflow: visible;
    background-color: #ffffff00 !important;
}

.vis-panel.vis-top {
    height: 20px !important;
}

.vis-panel.vis-bottom {
    height: 20px !important;
}

.vis-panel.vis-top > .vis-time-axis.vis-foreground > .vis-text.vis-minor.vis-odd,
.vis-panel.vis-top > .vis-time-axis.vis-foreground > .vis-text.vis-minor.vis-even
{
    top: -26px !important;
    color:rgb(255, 255, 255);
    margin-left: -12px !important;
    font-family: BebasNeue;
}

.vis-panel.vis-bottom > .vis-time-axis.vis-foreground > .vis-text.vis-minor.vis-odd,
.vis-panel.vis-bottom > .vis-time-axis.vis-foreground > .vis-text.vis-minor.vis-even
{
    top: -4px !important;
    color:rgb(255, 255, 255);
    margin-left: -12px !important;
    font-family: BebasNeue;
}

.vis-content {
    width: 100% !important;
    height: 32px !important;
    top: 6px !important;
    transform : none !important;
    background-color: #ffffff00 !important;
}

.vis-time-axis.vis-foreground {
    color: white;
}

.vis-time-axis.vis-background {

}

.vis-time-axis .vis-grid.vis-major {
    border-color: #fdf4f4 !important;
    border-width: 2px !important;
}

.vis-time-axis .vis-grid.vis-minor {
    border-color: #fdf4f4 !important;
    border-width: 2px !important;
}

.vis-current-time {
    background-color: #fdfdfd !important;
    width: 2px !important;
    z-index: 1;
    pointer-events: none;
}
.vis-current-time::before {
    content: "";
    position: absolute;
    left: -5px;
    top: 0;
    border-top: 6px solid #fff;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
}

.vis-current-time::after {
    content: "";
    position: absolute;
    left: -5px;
    bottom: 0;
    border-bottom: 6px solid #fff;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
}

.vis-custom-time {
    background-color: #fff !important;
    width: 2px !important;
    z-index: 1;
    pointer-events: none;
}

.vis-item {
    background-color: #d67737 !important;
    border-width: 0px !important;
    height: 100% !important;
}

.vis-item.vis-range.event-history-timeline-item.vis-readonly {
    height: 30px !important;
}
.vis-shadow.vis-top {
    visibility: hidden;
}

.vis-shadow.vis-bottom {
    visibility: hidden;
}

.timeline-vertical-line {
    position: relative;
    z-index: 3;
    top: 0;
    width: 2px;
    height: 100%;
    background-color: #ffffff; }
@media (min-width: 768px) {
    .timeline-vertical-line {
    left: 50%; 
    width: 2px;
    height: 100%;} 
}

@function vis-axis-text-color($value) {
    $color : null;
    @if $value == '12:00' {
        $color: #000;
    } 
    @else {
        $color: #fff;
    } 

    @return $color;
}