@import "../../views/App/app.scss";
@import "../../../node_modules/antd-mobile/dist/antd-mobile.min.css";

.shop-item {
    display: flex;
    width: 100%;
    @include shop_item_gradient;
    margin-bottom: 3%;
    border-radius: 12px 12px 12px 12px;
    @include desktopFull {
        border-radius: 20px 20px 20px 20px;
    }
    
    @include desktopHalf() {
        border-radius: 20px 20px 20px 20px;
    }

    @include tablet{
        border-radius: 20px 20px 20px 20px;
    }
}

.shop-item-preview {
    display: flex;
    width: 33%;
    margin: 3%;
    bottom: 0px;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.shop-item-detail {
    display: flex;
    width: 66%;
    height: 100%;
    padding: 3%;
    flex-direction: column;
}

.shop-item-title {
    font-size: 1rem;
    text-align: end;
    width: 100%;
    font-family: BebasNeue-bold;
    line-height: normal;
    margin-top: 10px;
    overflow: hidden;
    // white-space: nowrap;
    // text-overflow:ellipsis
    @include desktopFull {
        font-size: 1.5rem;
    }
    
    @include desktopHalf() {
        font-size: 1.25rem;
    }

    @include tablet{
        font-size: 1.2rem;
    }
}

.shop-item-version {
    width: 100%;
    text-align: end;
    font-family: BebasNeue-bold;
    font-size: 0.8rem;
    line-height: normal;
    overflow: hidden;
    // white-space: nowrap;
    text-overflow:ellipsis;

    @include desktopFull {
        font-size: 1rem;
    }
    
    @include desktopHalf() {
        font-size: 1rem;
    }

    @include tablet{
        font-size: 0.8rem;
    }
}

.shop-item-status {
    width: 100%;
    text-align: end;
    font-family: BebasNeue-bold;
    font-size: 0.8rem;
    line-height: normal;
    overflow: hidden;

    @include desktopFull {
        font-size: 1rem;
    }
    
    @include desktopHalf() {
        font-size: 1rem;
    }

    @include tablet{
        font-size: 0.8rem;
    }
}

.shop-item-prie {
    width: 100%;
    text-align: end;
    font-family: BebasNeue-bold;
    font-size: 0.8rem;
    line-height: normal;

    @include desktopFull {
        font-size: 1rem;
    }
    
    @include desktopHalf() {
        font-size: 1rem;
    }

    @include tablet{
        font-size: 0.8rem;
    }
}

.shop-item-desc {
    width: 100%;
    text-align: end;
    font-family: Helvetica-light;
    font-size: 0.7rem;
    line-height: normal;
    overflow: hidden;
    margin-top: 10px;
    // white-space: nowrap;
    // text-overflow:ellipsis
}

.shoplist-notify {
    width: 100%;
    font-family: BebasNeue-bold;
    font-size: 0.8rem;
    line-height: normal;
    margin-top: 15px;
    margin-left: 30px;
    margin-right: 30px;
}

.shop-tem-segment .am-segment .am-segment-item{
    border-color: #ffffff;
}

.am-segment-item{
    border: 0px solid #108ee9 !important;
    color: #030303;
    font-size: 1.5rem;
    font-family: BebasNeue;
}

.am-segment-item-selected {
    border: 1px solid #ffc65a !important;
    background: #00000000;
    color: #ffc65a !important;
    border-radius: 5px 5px 5px 5px !important;
}

.shop-item-sizes {
    width: 100%;
}

.shop-item-buynow {
    margin-top: 15px;
    bottom: 5px;
    font-family: BebasNeue-bold !important;
    color: black;
    border: none !important;
    font-size: 1rem;
    background: #ffc65a;
}

.am-button-primary:before{
    border: none !important;
}

.am-button-primary {
    border: none !important;
    background-color: #ffc65a !important;
}

.shop-item-qty {
    display: flex;
    width: 100%;
    text-align: center;
    font-family: BebasNeue;
}

.shop-item-qty-stepper {
    display: flex;
    height: 100%;
    font-family: BebasNeue;
    border-radius: 0px;
    border-width: 2px;
    border-color: black;
    border: 1px;
    border-style: solid;
    height: 30px;
    padding-left: 5px;
    background: #ffffff;
}
.shop-item-qty-txt{
    margin-top: 6%;
}
.shop-item-qty-stepper .am-stepper-handler{
    border: 0px;
}
.shop-item-paypal {
    width: 100%;
    font-size: 0.8rem;
}

.shop-item-paypal-check {
    width: 100%;
}

.shop-item-paypal-credit{
    width: 100%;
}

.ant-list-lg .ant-list-item {
    padding: 0 !important;
}

.ant-list-vertical .ant-list-item-extra {
    margin-left: 0;
}

.ant-list-split .ant-list-item{
    border-bottom: 0 !important;
}

.am-wingblank.am-wingblank-lg{
    margin-left: 30px !important;
    margin-right: 30px !important;
}

.shop-loading-container{
    margin-left: 48%;
}

.shoplist-loadmore{
    text-align: 'center';
    margin-top: 12;
    height: 32;
    line-height: '32px';
}