@import "../../views/App/app.scss";

.rivals-wrap {
    display: flex;
    justify-content: space-between;
    padding-left: 3%;
    padding-right: 3%;
    padding-bottom: 0%;
    padding-top: 0%;
}

.rival {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.quarter {
    text-align: center;
}

.right-team {
    text-align: right;
}

.rival-team-or-quarter {
    font-size: 1.5rem;
    font-weight: 600;
    font-family: BebasNeue;
    line-height: normal;
}

.rival-team-or-quarter-value {
    font-size: 3rem;
    font-weight: 600;
    line-height: 1;
    font-family: BebasNeue;
    line-height: normal;
}

.rivals-timer {
    display: flex;
    justify-content: center;
    padding-top: 10px;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 1.2rem;
    font-family: BebasNeue;
    line-height: normal;
}

.rival-game-list-container {
    position: relative;
    width: 100%;
    background-color: white;
    animation-name: slide;
    animation-duration: 0.2s;
}

@keyframes slide {
    from {height: 0;}
    to {height: 500px;}
}

.rivals-game-list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    list-style: none;
    padding: 0;
}

.rivals-game-list-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    transition-duration: 0.3;
    vertical-align: middle;
}

.rivals-game-list-item {
    border-radius: 0px 0px 0 0;
    box-shadow: 0 0 2px #ECECEC;
    -moz-box-shadow: 0 0 2px  rgba(0,0,0,0.2);
    -webkit-box-shadow: 0 0 2px  rgba(0,0,0,0.2);
    -o-box-shadow: 0 0 2px  rgba(0,0,0,0.2);
    height: 40px;
    text-align: center;
}

.score_live_icon {
    border-style: none;
    width: 40px;
    margin-left: 10px;
}