.header-tab-wrap {
  background-color: #282828;
  display: flex;
  justify-content: center;
  padding: 0 0;
  z-index: 10;
  position: fixed;
  width: 100%;
}

.header-tab {
  display: flex;
  justify-content: space-evenly;
}

.tab-wrap {
  border: 0px solid #ffffff;
  border-radius: 0 0 0 0;
  color: #ffffff;
  padding: 5px 10px;
  font-weight: 800;
  font-family: BebasNeue;
}

.tab {
  font-size: 1.2rem;
  font-family: BebasNeue;
}

.tab-wrap:active,
.tab-wrap:hover {
  color: #BAB8CB;
}

.active-tab:active,
.active-tab:hover {
  color: #20285D;
}

.middle-tab {
  border-radius: 0;
  border-left: none;
  border-right: none;
}

.last-tab {
  border-radius: 0 0 0 0;
}

.active-tab {
  background-color: white;
  color: #282828;
}

@media(max-width: 500px) {
  .header-tab {
    font-size: 1rem;
    font-family: BebasNeue;
  }
}