@import "../../views/App/app.scss";

.shop-carousel-item-container {
    position: relative;
    margin-left: 30px;
    margin-right: 30px;
}

.shop-carousel-item-container-bg{
    background-color: white;
}

.shop-carousel-item-bg {
    position: absolute;
    @include gradient;
    top: 30%;
    left: 0px;
    right: 0px;
    bottom: 0px;
    width: 100%;
    border-radius: 12px 12px 12px 12px;
    @include desktopFull {
        border-radius: 20px 20px 20px 20px;
    }
    
    @include desktopHalf() {
        border-radius: 20px 20px 20px 20px;
    }

    @include tablet{
        border-radius: 20px 20px 20px 20px;
    }
}

.shop-carousel-item-img{
    width: 100%;
    position: relative;
    z-index: 1;
}

.shop-carousel-item-img::before{
    content: "";
    display: block;
    padding-top: 90%;
}

.shop-carousel-item-img img{
    position: absolute;
    width: 58% !important;
    right: 10%;
    bottom: 20px;
}

.shop-carousel-item-name {
    position: absolute;
    vertical-align: bottom;
    font-family: BebasNeue;
    color: black;
    font-size: 1.2rem;
    line-height: normal;
    z-index: 2;
    bottom: 70px;
    margin-left: 12px;

    @include desktopFull {
        font-size: 2rem;
        bottom: 80px;
        margin-left: 22px;
    }
    
    @include desktopHalf() {
        font-size: 2rem;
        bottom: 80px;
        margin-left: 32px;
    }

    @include tablet{
        font-size: 2rem;
        bottom: 90px;
        margin-left: 22px;
    }
}

.shop-carousel-item-desc {
    position: absolute;
    vertical-align: bottom;
    font-family: BebasNeue;
    color: white;
    font-size: 8vw;
    line-height: normal;
    z-index: 2;
    bottom: 30px;
    margin-left: 10px;

    @include desktopFull {
        font-size: 3rem;
        bottom: 30px;
        margin-left: 20px;
    }
    
    @include desktopHalf() {
        font-size: 3rem;
        bottom: 30px;
        margin-left: 30px;
    }

    @include tablet{
        font-size: 3.5rem;
        bottom: 30px;
        margin-left: 20px;
    }
}

.carousel .control-dots .dot {
    margin: 0px 2px !important;
    width: 4px !important;
    height: 4px !important;
    box-shadow: none;

    @include desktopFull {
        margin: 0px 3px !important;
        width: 8px !important;
        height: 8px !important;
    }
    
    @include desktopHalf() {
        margin: 0px 3px !important;
        width: 6px !important;
        height: 6px !important;
    }

    @include tablet{
        width: 4px !important;
        height: 4px !important;
    }
}
