@import "../../views/App/app.scss";


.social-infinite-container{
    margin-top: 60px;
    margin-bottom: 20px;;
}

.social-item-container {
    background: #17202a;
    margin: 5%;
    border-radius: 20px 20px 20px 20px;
    width: 90%;
    height: 90%;
    top: 0;
    bottom: 0;
    overflow: hidden;
    font-family: BebasNeue;
    color: white;
}

.social-item-content-img {
    position: relative;
    width: 100%;
    padding-top: 0%;
    top: 0;
}

.carousel .slide iframe {
    margin: 0% !important;
}