@import "../../views/App/app.scss";

.arview-container {
    width: 100%;
    height: 100%;
}

.model-viewer{
    width: 100%;
    height: 100%;
}

#ar-button img {
    width: 15%;
    height: 15%;
    object-fit:cover;
    object-position: center;
    overflow: hidden;
    border: 0;
    padding-right: 5px;
}

#ar-button {
    display: flex;
    padding-top:5px;
    padding-bottom:5px; 
    padding-left: 0;
    padding-right: 0;
    background-color: #282828;
    color:white;
    border-radius: 4px;
    border: none;
    position: absolute;
    bottom: 1%;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.ar-button-text {
    text-align: center;
    line-height: 0.8rem;
}